.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

table.center {
    width:70%;
    margin-left:15%;
    margin-right:15%;
}


.error {
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  padding: 10px;
  margin-bottom: 10px;
}

.info {
  color: yellow;
  background: green;
  font-size: 20px;
  border-style: solid;
  padding: 10px;
  margin-bottom: 10px;
}
